import { createWebHistory, createRouter } from "vue-router";
import ListComponent from './components/ListComponent';
import HomeComponent from './components/HomeComponent';
import DetailComponent from './components/DetailComponent';
import AuthorComponent from './components/Detail/AuthorComponent';
import CommentComponent from './components/Detail/CommentComponent';

import NotFoundFor404 from './components/NotFoundFor404';

// 먼저 작성한 컴포넌트가 우선권을 갖는다 (404컴포넌트 적용가능. 마지막 컴포넌트로 등록)
const routes = [
  {
    path: "/list", // 경로접속시    
    component: ListComponent, //해당 컴포넌트 돌려줌
  },
  {
    path: "/", // 경로접속시    
    component: HomeComponent, //해당 컴포넌트 돌려줌
  },
  {
    path: "/detail/:id", // 경로접속시    
    component: DetailComponent, //해당 컴포넌트 돌려줌
    children:[{
        path: "comment",
        component: CommentComponent
    },
    {
        path: "author",
        component: AuthorComponent
    }]
  },
  {
    path: "/:anything(.*)", // 경로접속시    
    component: NotFoundFor404, //해당 컴포넌트 돌려줌
  }
];

// 라우터 생성 (createRouter 안에 설정 넣기)
const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 